<template>
  <div class="home" style="height: 100%;width: 100%">
    <Nav>
    </Nav>
    <div style="display: flex;justify-content: start;height: calc(100% - 61px)">
      <el-row style="width: 25%;" class="tac">
        <el-col :span="12" style="background-color:#545c64;width: 100%">
          <el-menu
                  active-text-color="#ffd04b"
                  background-color="#545c64"
                  class="el-menu-vertical-demo"
                  default-active="1"
                  text-color="#fff"
          >
            <el-menu-item index="1">
              <el-icon><shopping-bag /></el-icon>
              <span>商品管理</span>
            </el-menu-item>
            <el-menu-item index="2">
              <el-icon><user-filled /></el-icon>
              <span>职工管理</span>
            </el-menu-item>
            <el-menu-item index="3">
              <el-icon><avatar /></el-icon>
              <span>客户管理</span>
            </el-menu-item>
          </el-menu>
        </el-col>
      </el-row>
      <div style="width: 75%;position: relative">
        <el-button type="primary" @click="add" size="small" style="position: relative;top: 4%;left: 15%">新增</el-button>
        <el-row :gutter="20" style="width: 20%;position: absolute;right: 11%;top: 4%">
          <el-input v-model="inputSearch" size="small" placeholder="搜索" >
            <template #prefix>
              <el-icon @click="search" class="el-input__icon"><search /></el-icon>
            </template>
          </el-input>
        </el-row>
        <el-table :data="tableData" size="mini" max-height="400"  border style="position: relative;right: 10%;top: 5%">
          <el-table-column prop="gid" label="商品编号"  />
          <el-table-column prop="gname" label="商品名称"  />
          <el-table-column prop="gprice" label="商品价格"/>
          <el-table-column prop="gnum" label="商品数量" />
          <el-table-column prop="gtext" label="商品描述" />
          <el-table-column label="商品操作">
            <template #default="scope">
              <el-button type="success" size="small" @click="edit(scope.row)">编辑</el-button>
              <el-popconfirm title="确认删除吗？" @confirm="deleteRow(scope.row.gid)">
                <template #reference>
                  <el-button type="danger" size="small" >删除</el-button>
                </template>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
        <div class="demo-pagination-block" style="position: absolute;bottom: 1%;left: 25%">
          <span class="demonstration"></span>
          <el-pagination
                  v-model:currentPage="currentPage"
                  :page-sizes="[4, 8, 16]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next"
                  :total="total"
                  small
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
          >
          </el-pagination>
        </div>
      </div>
      <el-dialog
              v-model="dialogVisible"
              title="请编辑"
              width="30%"
              :before-close="handleClose"
      >
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="商品编号">
            <el-input v-model="form.gid"></el-input>
          </el-form-item>
          <el-form-item label="商品名称">
            <el-input v-model="form.gname"></el-input>
          </el-form-item>
          <el-form-item label="商品价格">
            <el-input v-model="form.gprice"></el-input>
          </el-form-item>
          <el-form-item label="商品数量">
            <el-input v-model="form.gnum"></el-input>
          </el-form-item>
          <el-form-item label="商品描述">
            <el-input v-model="form.gtext"></el-input>
          </el-form-item>
        </el-form>
        <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="save">保存</el-button>
      </span>
        </template>
      </el-dialog>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Nav from '@/components/nav.vue'
import request from "@/utils/request";
import {ShoppingBag,UserFilled,Avatar,Search} from '@element-plus/icons';

export default {
  name: 'Home',
  components: {
    Nav,ShoppingBag,UserFilled,Avatar,Search
  },
  data() {
    return {
      form: {},
      dialogVisible: false,
      inputSearch: '',
      total: 0,
      currentPage: 1,
      pageSize: 8,
      tableData: [],
      addType: false,
      editType: false,
    }
  },
  created() {
    this.load()
  },
  methods: {
    load() {
      request.get("/api/goods",{
        params: {
          currentPage: this.currentPage,
          pageSize: this.pageSize,
          search: this.inputSearch
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.records;
        this.total = res.data.total;
      })
    },
    add() {
      this.dialogVisible = true;
      this.form = {
        gid: 'g00',
        gtext: '干净又卫生',
      };
      this.addType = true;
    },
    edit(val) {
      this.dialogVisible = true;
      this.form = val;
      this.editType = true;
    },
    save() {
      if (this.editType) {
        request.put("/api/goods",this.form).then(res => {
          if (res.code === '0'){
            this.dialogVisible = false;
            this.load();
            this.editType = false;
            this.$message({
              type: "success",
              message: "更新成功"
            })
          } else {
            console.log(res);
          }
        });
      } else {
        request.post("/api/goods",this.form).then(res => {
          console.log(res);
          if (res.code === '0'){
            this.dialogVisible = false;
            this.load();
            this.addType = false;
            this.$message({
              type: "success",
              message: "新增成功"
            })
          } else {
            console.log(res);
          }
        });
      }
    },
    handleSizeChange(val) {             //改变每页数据条数
      this.pageSize = val;
      this.load()
    },
    handleCurrentChange(val) {          //改变当前页码
      this.currentPage = val;
      this.load()
    },
    search() {
      this.load()
    },
    deleteRow(val) {
      request.delete("/api/goods/" + val ).then(res => {
        if (res.code === '0'){
          this.load();
          this.$message({
            type: "success",
            message: "删除成功"
          })
        } else {
          console.log(res);
        }
      })
    },
  }
}
</script>
